import React, { useState } from 'react';
import { Helmet } from 'react-helmet';
import { NewsData } from '../data/NewsData';

const News = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(20);

  const indexOfLastData = currentPage * dataPerPage;
  const indexOfFirstData = indexOfLastData - dataPerPage;
  const currentData = NewsData.slice(indexOfFirstData, indexOfLastData);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <div>
      <Helmet>
        <title>News</title>
      </Helmet>
      <div className='main-container'>
        <header id="section-head">
          <div className="section-banner-content">
            <h1>News</h1>
          </div>
        </header>
        <div className="section">
          <table className='news-table'>
            <thead>
              <tr>
                <th>Date</th>
                <th>Source Name</th>
                <th>Article Name</th>
              </tr>
            </thead>
            <tbody>
              {currentData.map((NewsData, index) => (
                <tr key={index}>
                  <td>{NewsData.Date}</td>
                  <td>{NewsData.SourceName}</td>
                  <td><a href={NewsData.ArticleLink1} target="_blank" rel="noreferrer" > {NewsData.ArticleName}</a></td>
                </tr>
              ))}
            </tbody>
          </table>
          <div className="pagination-section">
            <ul className='news-pagination'>
              {[...Array(Math.ceil(NewsData.length / dataPerPage))].map((_, i) => (
                <li
                  key={i}
                >
                  <button
                    className='news-pagination-btn'
                    onClick={() => paginate(i + 1)}
                  >
                    {i + 1}
                  </button>
                </li>
              ))}
            </ul>

          </div>






          <div >

          </div>

        </div>
      </div>
    </div>
  )
}

export default News