import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { ResearchAreasData } from '../data/ResearchAreasData';
import EmptyList from '../components/EmptyList';
import { Helmet } from 'react-helmet';


function MyList({ items }) {
    return (
        <ol>
            {items.map((item, index) => (
                <li key={index}>{item}</li>
            ))}
        </ol>
    );
}

const ResearchAreaDetails = () => {
    const { PageLink } = useParams();
    const [research, setResearch] = useState(null);
    useEffect(() => {
        let research = ResearchAreasData.find((research) => research.PageLink === (PageLink));
        if (research) {
            setResearch(research);
        }
    }, [PageLink]);

    return (
        <div className='main-container'>
            {research ? (
                <div>
                    <Helmet>
                        <title>{research.Title}</title>
                    </Helmet>
                        <header id="section-head">
                            <div className="section-banner-content">
                                <h2>{research.Title}</h2>
                            </div>
                        </header>
                        <div className="section">
                        {/* <div className="section-heading">
                            <h1>{research.Title}</h1>
                        </div> */}

                        <div className="reading-content">
                            <div className="research-page-image">
                                <img src={research.Image} alt='' />
                            </div>

                            <div className="para-content">
                                {research.Para1 &&
                                    <p>{research.Para1}</p>
                                }
                            </div>

                            <div className="para-content">
                                {research.Para2 &&
                                    <p>{research.Para2}</p>
                                }
                            </div>

                            <div className="para-content">
                                {research.Para3 &&
                                    <p>{research.Para3}</p>
                                }
                            </div>

                            <div className="para-content">
                                {research.Para4 &&
                                    <p>{research.Para4}</p>
                                }
                            </div>

                            <div className="para-content">
                                {research.Questions &&
                                    <MyList items={research.Questions} />
                                }
                            </div>

                            <div className="para-content">
                                <h2>Related Papers and Links</h2>
                                {research.RelatedPapers &&
                                    // <MyList items={research.RelatedPapers} />
                                    <ol>
                                        {research.RelatedPapers.map((item, index) => (
                                            <a href={item} target="_blank" rel="noreferrer"><li key={index}>{item} </li></a>
                                        ))}
                                    </ol>
                                }
                            </div>

                        </div>






                    </div>




                </div>
            ) : (
                <EmptyList />
            )}

        </div>
    )
}

export default ResearchAreaDetails