import React, { useState, useEffect } from 'react';



const images=[require("../assets/slideshow/slideshow-1.png"),
require("../assets/slideshow/slideshow-2.png"),
require("../assets/slideshow/slideshow-3.png"),
require("../assets/slideshow/slideshow-4.png"),
require("../assets/slideshow/slideshow-5.png"),
require("../assets/slideshow/slideshow-6.png"),
require("../assets/slideshow/slideshow-7.png"),

    
]
function ImageSlider({ interval = 4000 }) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentImageIndex((currentImageIndex + 1) % images.length);
    }, interval);
    return () => clearInterval(intervalId);
  }, [currentImageIndex, images.length, interval]);

  const handlePrevClick = () => {
    setCurrentImageIndex((currentImageIndex + images.length - 1) % images.length);
  };

  const handleNextClick = () => {
    setCurrentImageIndex((currentImageIndex + 1) % images.length);
  };

  return (
    <div className='image-slideshow'>
      <button className='prev' onClick={handlePrevClick}></button>
      <img src={images[currentImageIndex]} alt="Slider" />
      <button className='next' onClick={handleNextClick}></button>
    </div>
  );
}

export default ImageSlider;
