import React from 'react';
import { Helmet } from 'react-helmet';
import { PeopleData } from '../../data/PeopleData';
import { Link } from 'react-router-dom';

const PeopleList = () => {
  return (
    <div>
      <Helmet>
        <title>People</title>
      </Helmet>
      <div className="main-container">
        <div className="section">
          <div className="section-heading">
            <h1>Current Members</h1>
          </div>
          <div className="grid-4">
            {PeopleData.map(people => {
              return (
                <div className="card-item">
                  <div className="card-item-img">
                    <img className='blogItem-cover' src={people.Image} alt='cover' />
                  </div>
                  <Link to={`/people/${people.Name}`} onClick={() => window.scrollTo(0, 0)}>
                    <h4>{people.Name}</h4>
                  </Link>
                  <p className='card-para-content'>{people.Designation}</p>
                  <p><span className='bold'>Research Interests: </span> {people.ResearchInterests}</p>
                  <p>Email:  <a href={`mailto:${people.Email}`}>{people.Email}</a></p>
                </div>
              )

            })}
          </div>
        </div>

      </div>
    </div>

  )
}

export default PeopleList