import React from 'react'
import { Helmet } from 'react-helmet';
import { PublicationsData } from '../data/PublicationsData';


function MyList({ items }) {
  return (
    <ol className='list-items'>
      {items.map((item, index) => (
        <li key={index}>{item}</li>
      ))}
    </ol>
  );
}

const Publications = () => {
  return (
    <div>
      <Helmet>
        <title>Publications</title>
      </Helmet>
      <div className='main-container'>
        <header id="section-head">
          <div className="section-banner-content">
            <h1>Publication</h1>
          </div>
        </header>

        <div className="section">
          <h1>Books, Chapter in Books, Expository and Review Articles</h1>
          <p className='para-content'>
            <MyList items={PublicationsData[0].BooksChapterExpositoryAndArticles} />
          </p>


        </div>

        <div className="section">
          <h1>Journal Articles (published/accepted)</h1>
          <p className='para-content'>
            <ol className='list-items'>
              {PublicationsData[1].JournalArticles.map(item => {
                return (
                  <li><a href={item.ArticleLink} target="_blank" rel="noreferrer">{item.ArticleTitle}</a> </li>
                )
              })}

            </ol>

          </p>



        </div>

        <div className="section">
          <h1>Invited papers in published conference</h1>
          <p className='para-content'>
            <MyList items={PublicationsData[2].InvitedPapers} />
          </p>


        </div>

        <div className="section">
          <h1>Conference Presentations/abstract</h1>
          <p className='para-content'>
            <MyList items={PublicationsData[3].Conference} />
          </p>


        </div>



      </div>
    </div>
  )
}

export default Publications