import React from 'react'
import { ExMembersData } from '../../data/ExMembersData'
import GoogleScholarIcon from "../../assets/social-icons/google-scholar.jpg"
import EmailIcon from "../../assets/social-icons/email.jpg"

const Alumni = () => {
    return (
        <div className="main-container">
            <div className="section">
                <div className="section-heading">
                    <h1>Alumni</h1>
                </div>
                <div className="grid-4">
                    {ExMembersData.map(member => {
                        return (
                            <div className="card-item">
                                <div className="card-item-img">
                                    <img src={member.Image} alt='cover' />
                                </div>

                                <h4>{member.Name}</h4>
                                <p>{member.Education}</p>
                                <p>{member.Year}</p>
                                
                                <div className="card-item-social">
                                {member.Email && <p><a href={`mailto:${member.Email}`} target="_blank" rel="noreferrer"><img src={EmailIcon} alt="Social Icon" title='Email' className="people-social-icons-img" /></a>
                                
                                </p>}

                                {member.SocialLink && <p><a href={member.SocialLink} target="_blank" rel="noreferrer"><img src={GoogleScholarIcon} alt="Social Icon" title='Google Scholar' className="people-social-icons-img" /></a></p>}

                                </div>
                                

                            </div>
                        )

                    })}
                </div>

            </div>
        </div>


    )
}

export default Alumni